import React from 'react'
import styled from 'styled-components'

import { Link } from 'gatsby'
import siteLogo from '../../images/envyforge-logo.png'
import ActionBar from '../elements/actionBar'

const HeaderWrapper = styled.div`
  grid-area: header;
  height: auto;
  font-size: 1.4em;
  color: white;
  img {
    margin-bottom: 0;
    height: 32px;
    padding: 4px 4px 4px 4px;
  }
`

const HeaderActionBarWrapper = styled.div`
  grid-area: headerTop;
  height: auto;
  padding: 10px 14px 4px 10px;
  font-size: 1.4em;
  background: rgb(0, 97, 158);
  color: white;
  img {
    margin-bottom: 0;
    height: 32px;
    padding: 0 0 0 4px;
  }
`

const HeaderContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding: 10px 0 10px 10px;
  justify-content: space-between;
`

const HeaderLeft = styled.div`
  grid-column: 1 / 1;
`

const HeaderRight = styled.div`
  grid-column: 3 / 3;
  padding-right: 10px;
`

const Header = () => (
  <HeaderWrapper>
    <HeaderActionBarWrapper>
      <ActionBar />
    </HeaderActionBarWrapper>
    <HeaderContainer>
      <HeaderLeft>
        <Link to="/">
          <img src={siteLogo} alt="Matt Ortiz Logo" />
        </Link>
      </HeaderLeft>
      <HeaderRight>
        <ActionBar />
      </HeaderRight>
    </HeaderContainer>
  </HeaderWrapper>
)

export default Header
