import React from 'react'
import styled from 'styled-components'

import { Link } from 'gatsby'

import facebookIcon from '../../images/social-icons/facebook.svg'
import googlePlusIcon from '../../images/social-icons/google.svg'
import instagramIcon from '../../images/social-icons/instagram.svg'

const ActionBarWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`;

const ActionBar = () => (
  <ActionBarWrapper>
    <Link to="/" >
      <img src={facebookIcon} alt="Facebook" />
    </Link>
    <Link to="/" >
      <img src={googlePlusIcon} alt="Google+" />
    </Link>
    <Link to="/" >
      <img src={instagramIcon} alt="Instagram" />
    </Link>
  </ActionBarWrapper>
)

export default ActionBar
